.pods-submittable-fields .panda-repeater-iframe {
  width: 100% !important;
  height: 100%;
  border-top: 1px solid #f1f1f1;
  float: left;
}

.pods-submittable-fields .panda-repeater-iframe body {
  background: none;
}

.center {
  text-align: center;
}

#toplevel_page_panda-pods-repeater-field {
  display: none;
}

.pprf-redorder-list {
  padding-inline-start: 0;
  margin-left: 0;
  margin-right: 0;
  padding-left: 0;
  padding-right: 0;
}

.pprf-redorder-list li {
  /*padding:5px;	

	border-left: 5px solid #46b450;*/
  float: left;
  width: 100%;
  margin: 0 0 2px 0;
}

.pprf-redorder-list.pandarf_order li {
  cursor: move;
  list-style: none;
}

.pprf-row {
  border: 1px solid #f1f1f1;
  width: calc(100% - 2px);
  margin-bottom: 2px;
}

.pprf-row iframe body .updated,
.pprf-row iframe body .update-nag {
  display: none;
}

.pprf-row iframe body #adminmenuback,
.pprf-row iframe body #adminmenuwrap,
.pprf-row iframe body #wpadminbar,
.pprf-row iframe body #wpfooter,
.pprf-row iframe body #screen-meta-links {
  display: none;
}

.pprf-row .button {
  background: none !important;
  border: none !important;
  box-shadow: none !important;
  float: left;
  height: 30px;
  min-height: initial;
  cursor: pointer;
  margin: 0;
}

.pprf-text-right {
  text-align: right;
}

.button.pprf-save-btn,
.button.pprf-trash-btn {
  border-right: 1px solid #e8e8e8 !important;
  margin: 0px;
}

.pprf-grey-bg {
  background-color: #f1f1f1;
}

.pprf-green-bg {
  background-color: #d9e6ff;
}

.pprf-purple-bg {
  background-color: #F0F5FF;
}

.pprf-white-bg {
  background-color: #ffffff;
}

.pprf-left-col {
  border-right: double #e8e8e8 thick;
}

.pprf-left-col .dashicons {
  height: 14px;
  width: 14px;
  font-size: 14px;
  margin-top: 4px;
}

.pprf-left-col .dashicons::before {
  font-size: 14px;
}

.pprf-right-col {
  width: 43px;
  padding: 0 !important;
  margin: 0;
}

.pprf-right-col .dashicons {
  width: 100%;
}

.button.pprf-redorder-btn,
.button.pprf-save-redorder-btn {
  background: #F60;
  color: #fff;
  border-radius: 5px;
  font-weight: bold;
  box-shadow: 2px 2px 2px 2px #ccc;
  cursor: pointer;
  margin-bottom: 5px;
}

.pprf-expand-bar {
  background-color: #e3eef4;
  height: 20px;
  cursor: pointer;
  color: #0073aa;
  font-size: 11px;
  width: 100%;
}

.pprf-save-icon {
  height: 16px !important;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.hidden {
  display: none;
}

.toplevel_page_panda-pods-repeater-field #wpcontent {
  margin-left: 0;
}

.toplevel_page_panda-pods-repeater-field {
  background: #fff;
}

.pods-form-front .pods-form-ui-row-type-pandarepeaterfield .pods-field-input {
  width: 80%;
}

.pprf-tab {
  float: left;
  padding: 5px 10px 3px;
  border: 1px solid #e8e8e8;
  border-bottom: none;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  margin: 0 3px 5px 0;
  cursor: pointer;
  opacity: 0.3;
}

.pprf-tab:hover {
  opacity: 0.8;
}

.pprf-tab.active {
  background-color: #0073AA;
  color: #fff;
  opacity: 1;
}

.pprf-load-more-wrap {
  line-height: 28px;
}

.pprf-load-more-wrap .pprf-input,
.pprf-load-more-wrap input[type="number"].pprf-input {
  width: 50px;
}

.pprf-load-more-wrap .pprf-input,
.pprf-load-more-wrap .pprf-select {
  border: 1px solid #e8e8e8;
  border-radius: 5px;
}

.pprf-load-more-wrap .pprf-load-more-btn,
.pprf-btn {
  padding-left: 5px;
  padding-right: 5px;
  border: 1px solid #e8e8e8;
  border-radius: 5px;
  cursor: pointer;
  background: #F0F5FF;
}

.pprf-load-more-wrap .pprf-load-more-btn:hover,
.pprf-btn:hover {
  background: #F1F1F1;
}

.pprf-btn {
  padding: 1px 5px 4px 5px;
}

select.pprf-in-iframe-sel {
  height: 23px;
  margin-right: 10px;
  margin-bottom: 5px;
  padding: 0;
  font-size: 14px;
}

.pprf-left {
  float: left;
}

.pprf-order-info {
  font-size: 14px;
}

.pods-form-front .pods-field .pods-field-input .pprf-load-more-wrap .panda-repeater-to-load {
